.box {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 350px;
    background-color: rgba(255, 255, 255, 0.13);
    backdrop-filter: blur(10px);
    border: 2px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 40px rgba(8, 7, 16, 0.6);
    padding: 50px 35px;
    z-index: 1;
    border-radius: 20px;
}

.form {
    display: grid;
    gap: 16px;
    margin-top: 24px;
} 

.errorMsq {
    margin-top: 15px;
    text-align: center;
    font-size: 13px;
    color: red;
}

@media (max-width: 500px) {
    .box {
        width: 75%;
        padding: 35px 20px
    }
}

@media (max-width: 375px) {
    .box {
        width: 90%;
    }
}
